"use client";
/* eslint-disable @next/next/no-img-element */

import { useState, useEffect, useRef } from "react";
import { createClient } from "@/lib/supabase/browserClient";
import { useRouter } from "next/navigation";
import Modal from "@/app/ui/Modal";

interface UserProfile {
  id: string;
  email: string;
  first_name?: string;
  last_name?: string;
  age?: number;
  sex?: string;
  bio?: string;
  avatar_url?: string;
}

export default function EditProfilePage() {
  const [profile, setProfile] = useState<UserProfile | null>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [errors, setErrors] = useState<{ [key: string]: string }>({});
  const [message, setMessage] = useState<string | null>(null);
  const [imagePreview, setImagePreview] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const router = useRouter();
  const [showPasswordModal, setShowPasswordModal] = useState(false);
  const [passwordData, setPasswordData] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });
  const [passwordErrors, setPasswordErrors] = useState<{ [key: string]: string }>({});
  const [passwordMessage, setPasswordMessage] = useState<string | null>(null);
  const [updatingPassword, setUpdatingPassword] = useState(false);

  useEffect(() => {
    loadProfile();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const loadProfile = async () => {
    try {
      const supabase = createClient();
      const { data: { user }, error: authError } = await supabase.auth.getUser();
      
      if (authError || !user) {
        router.push("/auth/login");
        return;
      }

      // Get user profile from profiles table
      const { data: profileData, error: profileError } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', user.id)
        .single();

      if (profileError && profileError.code !== 'PGRST116') {
        console.error('Error loading profile:', profileError);
      }

      const userProfile: UserProfile = {
        id: user.id,
        email: user.email || '',
        first_name: profileData?.first_name || '',
        last_name: profileData?.last_name || '',
        age: profileData?.age || undefined,
        sex: profileData?.sex || '',
        bio: profileData?.bio || '',
        avatar_url: profileData?.avatar_url || ''
      };

      setProfile(userProfile);
      setImagePreview(userProfile.avatar_url || null);
    } catch (error) {
      console.error('Error loading profile:', error);
      setMessage('Failed to load profile');
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (field: string, value: string) => {
    if (!profile) return;
    
    setProfile(prev => ({
      ...prev!,
      [field]: field === 'age' ? (value ? parseInt(value) : undefined) : value
    }));
    setErrors(prev => ({
      ...prev,
      [field]: ''
    }));
  };


  const handleImageUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // Validate file type
    if (!file.type.startsWith('image/')) {
      setErrors(prev => ({ ...prev, avatar: 'Please select a valid image file' }));
      return;
    }

    // Validate file size (max 5MB)
    if (file.size > 5 * 1024 * 1024) {
      setErrors(prev => ({ ...prev, avatar: 'Image size must be less than 5MB' }));
      return;
    }

    try {
      setSaving(true);
      const supabase = createClient();
      
      // Create unique filename
      const fileExt = file.name.split('.').pop();
      const fileName = `${profile!.id}-${Date.now()}.${fileExt}`;
      const filePath = `${fileName}`;

      // Upload to Supabase Storage
      const { error: uploadError } = await supabase.storage
        .from('avatars')
        .upload(filePath, file);

      if (uploadError) {
        throw uploadError;
      }

      // Get public URL
      const { data: { publicUrl } } = supabase.storage
        .from('avatars')
        .getPublicUrl(filePath);

      // Update profile with new avatar URL
      const { error: updateError } = await supabase
        .from('profiles')
        .upsert({
          id: profile!.id,
          avatar_url: publicUrl,
          updated_at: new Date().toISOString()
        });

      if (updateError) {
        throw updateError;
      }

      setProfile(prev => ({ ...prev!, avatar_url: publicUrl }));
      setImagePreview(publicUrl);
      setErrors(prev => ({ ...prev, avatar: '' }));
    } catch (error) {
      console.error('Error uploading image:', error);
      setErrors(prev => ({ ...prev, avatar: 'Failed to upload image' }));
    } finally {
      setSaving(false);
    }
  };

  const handlePasswordUpdate = async (e: React.FormEvent) => {
    e.preventDefault();
    setPasswordMessage(null);
    setPasswordErrors({});

    // Validate form
    const newErrors: { [key: string]: string } = {};

    if (!passwordData.currentPassword) {
      newErrors.currentPassword = 'Current password is required';
    }

    if (!passwordData.newPassword) {
      newErrors.newPassword = 'New password is required';
    } else if (passwordData.newPassword.length < 6) {
      newErrors.newPassword = 'Password must be at least 6 characters';
    }

    if (!passwordData.confirmPassword) {
      newErrors.confirmPassword = 'Please confirm your password';
    } else if (passwordData.newPassword !== passwordData.confirmPassword) {
      newErrors.confirmPassword = 'Passwords do not match';
    }

    // Check if new password is same as current password
    if (passwordData.currentPassword && passwordData.newPassword && 
        passwordData.currentPassword === passwordData.newPassword) {
      newErrors.newPassword = 'New password must be different from current password';
    }

    setPasswordErrors(newErrors);
    if (Object.keys(newErrors).length > 0) return;

    try {
      setUpdatingPassword(true);
      const supabase = createClient();

      // Get current user email
      const { data: { user }, error: userError } = await supabase.auth.getUser();
      if (userError || !user || !user.email) {
        throw new Error('Unable to verify user identity');
      }

      // Verify current password by attempting to sign in
      const { error: verifyError } = await supabase.auth.signInWithPassword({
        email: user.email,
        password: passwordData.currentPassword
      });

      if (verifyError) {
        setPasswordErrors({ currentPassword: 'Current password is incorrect' });
        return;
      }

      // Update password using Supabase
      const { error } = await supabase.auth.updateUser({
        password: passwordData.newPassword
      });

      if (error) {
        throw error;
      }

      setPasswordMessage('Password updated successfully!');
      // Reset form
      setPasswordData({
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      });
      // Close modal after a short delay
      setTimeout(() => {
        setShowPasswordModal(false);
        setPasswordMessage(null);
      }, 1500);
    } catch (error: any) {
      console.error('Error updating password:', error);
      if (!error.message.includes('incorrect')) {
        setPasswordMessage(error.message || 'Failed to update password');
      }
    } finally {
      setUpdatingPassword(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!profile) return;

    // Validate form
    const newErrors: { [key: string]: string } = {};
    
    if (profile.first_name && profile.first_name.length < 1) {
      newErrors.first_name = 'First name is required';
    }
    
    if (profile.last_name && profile.last_name.length < 1) {
      newErrors.last_name = 'Last name is required';
    }
    
    if (profile.bio && profile.bio.length > 500) {
      newErrors.bio = 'Bio must be less than 500 characters';
    }

    if (profile.age && (profile.age < 13 || profile.age > 120)) {
      newErrors.age = 'Age must be between 13 and 120';
    }

    setErrors(newErrors);
    if (Object.keys(newErrors).length > 0) return;

    try {
      setSaving(true);
      setMessage(null);
      const supabase = createClient();

      // Update profile in database
      const { error } = await supabase
        .from('profiles')
        .upsert({
          id: profile.id,
          first_name: profile.first_name,
          last_name: profile.last_name,
          age: profile.age,
          sex: profile.sex,
          bio: profile.bio,
          updated_at: new Date().toISOString()
        });

      if (error) {
        throw error;
      }

      setMessage('Profile updated successfully!');
      setTimeout(() => router.push('/dashboard'), 1500);
    } catch (error) {
      console.error('Error updating profile:', error);
      setMessage('Failed to update profile');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div className="ai-assistant-bg flex items-center justify-center">
        <div className="text-center bg-ai-card border border-ai rounded-2xl px-6 py-8 shadow-ai">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-[#8c52ff] mx-auto mb-4"></div>
          <p className="text-ai-muted">Loading profile...</p>
        </div>
      </div>
    );
  }

  if (!profile) {
    return (
      <div className="ai-assistant-bg flex items-center justify-center">
        <div className="text-center bg-ai-card border border-ai rounded-2xl px-6 py-8 shadow-ai space-y-4">
          <p className="text-red-400">Failed to load profile</p>
          <button 
            onClick={() => router.push('/dashboard')}
            className="px-4 py-2 bg-gradient-to-r from-fuchsia-500 to-violet-600 text-white rounded-md hover:opacity-90 transition"
          >
            Back to Dashboard
          </button>
        </div>
      </div>
    );
  }


  return (
    <div className="ai-assistant-bg">
      <div className="container py-10">
        <div className="max-w-2xl mx-auto bg-ai-card border border-ai shadow-ai rounded-2xl p-6 md:p-8 backdrop-blur">
          {/* Header */}
          <div className="text-center mb-8">
            <div className="inline-flex items-center gap-2 mb-4">
              <span className="h-8 w-8 rounded-xl bg-gradient-to-r from-fuchsia-500 to-violet-600 grid place-items-center text-white">
                <svg width="18" height="18" viewBox="0 0 24 24" fill="none" aria-hidden>
                  <path d="M12 21s7-4.5 7-10a7 7 0 10-14 0c0 5.5 7 10 7 10z" stroke="currentColor" strokeWidth="1.6"/>
                  <circle cx="12" cy="11" r="2" fill="currentColor" />
                </svg>
              </span>
              <span className="text-lg font-semibold bg-gradient-to-r from-fuchsia-500 to-violet-600 bg-clip-text text-transparent">Lokalista</span>
            </div>
            <h1 className="h1 mb-2">Edit Profile</h1>
            <p className="text-muted">Update your personal information and preferences</p>
          </div>

          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="p-6 md:p-7 rounded-xl border border-ai bg-ai-surface shadow-ai space-y-6">
              
              {/* Profile Picture Section */}
              <div>
                <label className="block text-sm font-medium mb-3 text-ai-muted">Profile Picture</label>
                <div className="flex items-center gap-4">
                  <div className="relative">
                    <div className="w-20 h-20 rounded-full bg-ai-muted flex items-center justify-center overflow-hidden border border-ai">
                      {imagePreview ? (
                        <img 
                          src={imagePreview} 
                          alt="Profile" 
                          className="w-full h-full object-cover"
                        />
                      ) : (
                        <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                        </svg>
                      )}
                    </div>
                  </div>
                  <div>
                    <button
                      type="button"
                      onClick={() => fileInputRef.current?.click()}
                      disabled={saving}
                      className="px-4 py-2 bg-gradient-to-r from-fuchsia-500 to-violet-600 text-white rounded-md hover:opacity-90 disabled:opacity-60 text-sm transition"
                    >
                      {saving ? 'Uploading...' : 'Change Photo'}
                    </button>
                    <p className="text-xs text-ai-muted mt-1">JPG, PNG up to 5MB</p>
                  </div>
                </div>
                <input
                  ref={fileInputRef}
                  type="file"
                  accept="image/*"
                  onChange={handleImageUpload}
                  className="hidden"
                />
                {errors.avatar && <div className="text-sm text-red-600 mt-2">{errors.avatar}</div>}
              </div>

              {/* Basic Information */}
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium mb-2 text-ai-muted" htmlFor="email">Email</label>
                  <input
                    id="email"
                    type="email"
                    value={profile.email}
                    disabled
                    className="w-full h-11 px-3 rounded-md border border-ai bg-ai-muted text-ai-muted"
                  />
                  <p className="text-xs text-ai-muted mt-1">Email cannot be changed</p>
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2 text-ai-muted">Password</label>
                  <button
                    type="button"
                    onClick={() => setShowPasswordModal(true)}
                    className="px-4 py-2 text-sm border border-ai rounded-md hover:bg-ai-muted transition text-foreground"
                  >
                    Reset Password
                  </button>
                  <p className="text-xs text-ai-muted mt-1">Click to change your password</p>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium mb-2 text-ai-muted" htmlFor="first_name">First Name</label>
                    <input
                      id="first_name"
                      type="text"
                      value={profile.first_name || ''}
                      onChange={(e) => handleInputChange('first_name', e.target.value)}
                      placeholder="Enter your first name"
                      className="w-full h-11 px-3 rounded-md border border-ai focus-ring bg-transparent text-foreground"
                    />
                    {errors.first_name && <div className="text-sm text-red-600 mt-1">{errors.first_name}</div>}
                  </div>

                  <div>
                    <label className="block text-sm font-medium mb-2 text-ai-muted" htmlFor="last_name">Last Name</label>
                    <input
                      id="last_name"
                      type="text"
                      value={profile.last_name || ''}
                      onChange={(e) => handleInputChange('last_name', e.target.value)}
                      placeholder="Enter your last name"
                      className="w-full h-11 px-3 rounded-md border border-ai focus-ring bg-transparent text-foreground"
                    />
                    {errors.last_name && <div className="text-sm text-red-600 mt-1">{errors.last_name}</div>}
                  </div>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium mb-2 text-ai-muted" htmlFor="age">Age</label>
                    <input
                      id="age"
                      type="number"
                      value={profile.age || ''}
                      onChange={(e) => handleInputChange('age', e.target.value)}
                      placeholder="Your age"
                      min="13"
                      max="120"
                      className="w-full h-11 px-3 rounded-md border border-ai focus-ring bg-transparent text-foreground"
                    />
                    {errors.age && <div className="text-sm text-red-600 mt-1">{errors.age}</div>}
                  </div>

                  <div>
                    <label className="block text-sm font-medium mb-2 text-ai-muted" htmlFor="sex">Gender</label>
                    <select
                      id="sex"
                      value={profile.sex || ''}
                      onChange={(e) => handleInputChange('sex', e.target.value)}
                      className="w-full h-11 px-3 rounded-md border border-ai focus-ring bg-transparent text-foreground"
                    >
                      <option value="">Select gender</option>
                      <option value="male">Male</option>
                      <option value="female">Female</option>
                      <option value="other">Other</option>
                      <option value="prefer_not_to_say">Prefer not to say</option>
                    </select>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium mb-2 text-ai-muted" htmlFor="bio">Bio</label>
                  <textarea
                    id="bio"
                    value={profile.bio || ''}
                    onChange={(e) => handleInputChange('bio', e.target.value)}
                    placeholder="Tell us about yourself..."
                    rows={3}
                    maxLength={500}
                    className="w-full px-3 py-2 rounded-md border border-ai focus-ring resize-none bg-transparent text-foreground"
                  />
                  <div className="flex justify-between text-xs text-ai-muted mt-1">
                    <span>Optional</span>
                    <span>{(profile.bio || '').length}/500</span>
                  </div>
                  {errors.bio && <div className="text-sm text-red-600 mt-1">{errors.bio}</div>}
                </div>
              </div>
            </div>


            {/* Action Buttons */}
            <div className="flex gap-3">
              <button
                type="submit"
                disabled={saving}
                className="flex-1 h-11 rounded-md bg-gradient-to-r from-fuchsia-500 to-violet-600 text-white hover:opacity-90 disabled:opacity-60 transition"
              >
                {saving ? 'Saving...' : 'Save Changes'}
              </button>
              <button
                type="button"
                onClick={() => router.push('/dashboard')}
                className="px-6 h-11 rounded-md border border-ai hover:bg-ai-muted transition text-ai-muted"
              >
                Cancel
              </button>
            </div>

            {message && (
              <div className={`text-center text-sm p-3 rounded-md ${
                message.includes('success') 
                  ? 'bg-green-50 text-green-700 border border-green-200' 
                  : 'bg-red-50 text-red-700 border border-red-200'
              }`}>
                {message}
              </div>
            )}
          </form>

          <div className="mt-6 text-center">
            <a href="/dashboard" className="text-sm underline underline-offset-4 text-[#8c52ff]">
              Back to Dashboard
            </a>
          </div>
        </div>
      </div>

      {/* Password Reset Modal */}
      <Modal
        open={showPasswordModal}
        onClose={() => {
          setShowPasswordModal(false);
          setPasswordData({ currentPassword: '', newPassword: '', confirmPassword: '' });
          setPasswordErrors({});
          setPasswordMessage(null);
        }}
        title="Reset Password"
        showCloseButton={false}
        className="max-w-md p-6 bg-ai-card border border-ai"
      >
        <form onSubmit={handlePasswordUpdate} className="space-y-4">
          <div>
            <label className="block text-sm font-medium mb-2 text-ai-muted" htmlFor="currentPassword">
              Current Password
            </label>
            <input
              id="currentPassword"
              type="password"
              value={passwordData.currentPassword}
              onChange={(e) => {
                setPasswordData(prev => ({ ...prev, currentPassword: e.target.value }));
                setPasswordErrors(prev => ({ ...prev, currentPassword: '' }));
              }}
              placeholder="Enter your current password"
              className="w-full h-11 px-3 rounded-md border border-ai focus-ring bg-transparent text-foreground"
            />
            {passwordErrors.currentPassword && (
              <div className="text-sm text-red-600 mt-1">{passwordErrors.currentPassword}</div>
            )}
          </div>

          <div>
            <label className="block text-sm font-medium mb-2 text-ai-muted" htmlFor="newPassword">
              New Password
            </label>
            <input
              id="newPassword"
              type="password"
              value={passwordData.newPassword}
              onChange={(e) => {
                setPasswordData(prev => ({ ...prev, newPassword: e.target.value }));
                setPasswordErrors(prev => ({ ...prev, newPassword: '' }));
              }}
              placeholder="Enter new password"
              className="w-full h-11 px-3 rounded-md border border-ai focus-ring bg-transparent text-foreground"
            />
            {passwordErrors.newPassword && (
              <div className="text-sm text-red-600 mt-1">{passwordErrors.newPassword}</div>
            )}
          </div>

          <div>
            <label className="block text-sm font-medium mb-2 text-ai-muted" htmlFor="confirmPassword">
              Confirm New Password
            </label>
            <input
              id="confirmPassword"
              type="password"
              value={passwordData.confirmPassword}
              onChange={(e) => {
                setPasswordData(prev => ({ ...prev, confirmPassword: e.target.value }));
                setPasswordErrors(prev => ({ ...prev, confirmPassword: '' }));
              }}
              placeholder="Confirm new password"
              className="w-full h-11 px-3 rounded-md border border-ai focus-ring bg-transparent text-foreground"
            />
            {passwordErrors.confirmPassword && (
              <div className="text-sm text-red-600 mt-1">{passwordErrors.confirmPassword}</div>
            )}
          </div>

          {passwordMessage && (
            <div className={`text-center text-sm p-3 rounded-md ${
              passwordMessage.includes('success') 
                ? 'bg-green-50 text-green-700 border border-green-200' 
                : 'bg-red-50 text-red-700 border border-red-200'
            }`}>
              {passwordMessage}
            </div>
          )}

          <div className="flex gap-3 mt-6">
            <button
              type="submit"
              disabled={updatingPassword}
              className="flex-1 h-11 rounded-md bg-gradient-to-r from-fuchsia-500 to-violet-600 text-white hover:opacity-90 disabled:opacity-60 transition"
            >
              {updatingPassword ? 'Updating...' : 'Update Password'}
            </button>
            <button
              type="button"
              onClick={() => {
                setShowPasswordModal(false);
                setPasswordData({ currentPassword: '', newPassword: '', confirmPassword: '' });
                setPasswordErrors({});
                setPasswordMessage(null);
              }}
              className="px-6 h-11 rounded-md border border-ai hover:bg-ai-muted transition text-ai-muted"
            >
              Cancel
            </button>
          </div>
        </form>
      </Modal>
    </div>
  );
}
